@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:400,400i);
@import url(https://fonts.googleapis.com/css?family=Heebo:100,300,400);
:root {
  --lt-pink: #FFEBFF;
  --med-pink: #D6B4D6;
  --bg-navy: #1B182E;
}

* {
  box-sizing: border-box;
}

html, body {
  background: #0b0a13;
  margin: 0 auto;
  height: 100%;
  font-family: "Roboto Mono", monospace;
  color: #D6B4D6;
  color: var(--med-pink);
}

body {
  position: relative;
  height: 100%;
}

p {
  color: #D6B4D6;
  color: var(--med-pink);
  font-size: 0.8em;
}

.page-container {
  position: absolute;
  left: 5.0em;
  top: 4.0em;
  width: calc(100% - 10.0em);
  height: calc(100% - 8.0em);
}

@media screen and (max-width: 600px) {
  .page-container {
    left: 1.4em;
    width: calc(100% - 2.8em);
    height: calc(100% - 1.0em);
  }
}

#canvas {
  position: absolute;
}
.nav {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 3em;
}

span#name-label {
  position: absolute;
  left: 0;
}

span#name-label:hover {
  cursor: default;
}

.nav-items ul {     
  margin: 0;
  padding: 0;
}

.nav-items li {
  list-style-type: none;
  display: inline-block;
  position: relative;
  margin-left: 4em;
}
 
a, a:visited { 
  text-decoration: none;
  color: var(--med-pink);
  text-transform: uppercase;
  font-size: 0.9em;
  letter-spacing: 1.0px;
}

.underline:hover:after,
.underline:focus:after,
.underline:active:after {
  width: 100%;
}

.underline:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -3px;
  height: 1px;
  background: var(--lt-pink);
  width: 0;
  -webkit-transition: width 0.25s;
  transition: width 0.25s;
}

@media screen and (max-width: 600px) {
  .underline:hover:after,
  .underline:focus:after,
  .underline:active:after {
    width: 0%;
  }
  span#name-label {
    display: none;
  }
}
#info {
  max-width: 460px;
  line-height: 22px;
  position: absolute;
  bottom: 0;
}

.borderline {
  position: relative;
  height: 2px;
  margin-bottom: 24px;
  background: var(--med-pink);
}

span#info-label {
  color: var(--lt-pink);
  letter-spacing: 1.2px;
  font-size: 22px;
}

div#info-content {
  margin-top: 24px;
  margin-bottom: 28px;
}

div#info-content p {
  font-size: 0.8em;
}

img#ig-logo {
  height: 22px;
  width: 22px;
  margin-right: 16px;
}

img#email-logo {
  height: 22px;
}
.inner-page-container {
  overflow-x: auto;
  background: #0b0a13;
}

.page-header {
  position: relative;
}

h1.page-header-label {
  font-size: 20px;
  color: var(--lt-pink);
  display: inline-block;
  font-weight: 400;
  font-family: "Heebo", sans-serif;
  margin-bottom: 4px;
}

div.page-header-divider {
  display: inline-block;
	height: 2px;
	background: var(--lt-pink);
  width: 48px;
  left: 128px;
	position: absolute;
	top: 50%;
}

div.page-content {
  line-height: 26px;
  max-width: 580px;
}

div.page-content.about p {
  font-family: "Heebo", sans-serif;
  font-size: 1.2em;
  line-height: 1.4em;
  font-weight: 300;
}

/* Specific to Work page */
div.work-page-content {
  margin-top: 48px;
}

@media screen and (max-width: 600px) {
  .inner-page-container {
    margin-bottom: 0px;
  }
  div.page-content {
    margin-left: 0px;
    margin-right: 0px;
  }

}
.projects-container {
  margin-top: 2.4em;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}

.page-header.work {
  border-bottom: 1px solid;
}

.project-detail-container {
  max-width: 75%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1em;
}

.project-content p {
  font-size: 0.775em;
  line-height: 1.6em;
  color: var(--med-pink);
}

.project-content {
  margin-bottom: 3.5em;
}

.project-container {
  overflow: hidden;
  margin-bottom: 64px;
}
.project-img {
  width: 72%;
  float: left;
}

img.project-highlight {
  width: 100%;
  height: auto;
}

.project-meta {
  float: right;
  width: 25%;
  overflow:hidden;
}

.project-description {
  border: 2px solid var(--med-pink);
  padding: 12px;
}

.proj-img {
  margin-top: 32px;
  width: 100%;
  max-width: 100%;
}

.project-description {
  margin-top: -2px;
}

.project-description p {
  color: var(--lt-pink);
  margin: 0px;
  line-height: 22px;
}

/* GRID VIEW */
.content-grid-item-outer {
  width: 30%;
  margin-bottom: 6em;
  padding: 0px;  
  min-width: 400px;
}

.content-grid-item-outer.placeholder {
  margin-bottom: 0px;
}

.project-img-grid {
  width: 100%;
}

.project-meta-container {
  margin-top: 1.2em;
  position: relative;
}

.project-name {
  text-transform: uppercase;
  color: var(--med-pink);
  font-size: 1.1em;
  letter-spacing: 0.02em;
}

a.project-link {
  color: var(--lt-pink);
  font-size: 16px;
  display: flex;
  flex-direction: column;
  position: absolute;
  font-family: "Heebo", sans-serif;
  text-transform: none;
}

a.project-link:after {
  content: "";
  bottom: -3px;
  height: 1px;
  width: 100%;
  background: var(--med-pink);
}

.project-desc-container {
  padding-top: 2.2em;
}

.project-desc-container p {
  margin: 0px;
  line-height: 1.6em;
  font-size: 0.775em;
  color: var(--med-pink);
}

.project-detail-link {
  display: inline-block;
  padding: 8px 16px;
  font-size: 1em;
  margin-top: 36px;
  margin-bottom: 36px;
  border: 1px solid var(--med-pink);
  border-radius: 3px;
  font-size: 0.875em;
}

.project-detail-link:hover {
  background: var(--med-pink);
  color: var(--bg-navy);
}

div.vid {
  margin-bottom: 48px;
  padding:75% 0 0 0;
  position:relative;
}

.player {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
}

@media screen and (max-width: 980px) {
  .content-grid-item-outer {
    width: 85%;
    min-width: 100%;
    margin-bottom: 5em;
  }
  .placeholder {
    margin-bottom: 0;
    display: none;
  }
  .project-detail-container {
    max-width: 95%;
    margin-top: 0px;
  }
  .back-icon {
    display: none;
  }
}

@media screen and (max-width: 1400px) {
  .content-grid-item-outer {
    width: 46%;
  }
}

